.itemSearch {
	width: 100%;
	height: 35px;
	padding: 0px 20px;
	color: #2d2d2d;
	display: flex;
	align-items: center;
	cursor: pointer;
	transition: all 0.4s;

	&:hover {
		transition: all 0.4s;
		background: #c2dfe3;
	}

	p {
		font-weight: 500;
		font-size: 20px;
		color: #5c6b73;
	}
}
